import { BrowserBarcodeReader, VideoInputDevice, BrowserQRCodeSvgWriter } from '@zxing/library';


window.addEventListener('load', function () {
    const codeReader = new BrowserBarcodeReader()
    console.log('ZXing code reader initialized')
    codeReader.getVideoInputDevices()
        .then((videoInputDevices) => {
            let deviceId = videoInputDevices[0].deviceId
            if (videoInputDevices.length > 1) {
                const sourceSelect = document.getElementById('sourceSelect')
                videoInputDevices.forEach((element) => {
                    const sourceOption = document.createElement('option')
                    sourceOption.text = element.label
                    sourceOption.value = element.deviceId
                    if (element.label.match(/back/)) {
                        deviceId = element.deviceId;
                        sourceOption.selected = true;
                    }
                    sourceSelect.appendChild(sourceOption)
                })
                const sourceSelectPanel = document.getElementById('sourceSelectPanel')
                sourceSelectPanel.style.display = 'block'
            }
            codeReader.decodeFromInputVideoDevice(deviceId, 'video').then((result) => {
                console.log(result)
                alert(result.text);
                document.getElementById('result').textContent = result.text
            }).catch((err) => {
                console.error(err)
                document.getElementById('result').textContent = err
            })
            console.log(`Started continous decode from camera with id ${deviceId}`)
            document.getElementById('sourceSelect').addEventListener('change', function(event) {
                document.getElementById('result').textContent = '';
                codeReader.reset();
                codeReader.decodeFromInputVideoDevice(event.target.value, 'video').then((result) => {
                    console.log(result)
                    alert(result.text);
                    document.getElementById('result').textContent = result.text
                }).catch((err) => {
                    console.error(err)
                    document.getElementById('result').textContent = err
                })    
            });
            // document.getElementById('startButton').addEventListener('click', () => {
            //     codeReader.decodeFromInputVideoDevice(firstDeviceId, 'video').then((result) => {
            //         console.log(result)
            //         this.alert(result.text);
            //         document.getElementById('result').textContent = result.text
            //     }).catch((err) => {
            //         console.error(err)
            //         document.getElementById('result').textContent = err
            //     })
            //     console.log(`Started continous decode from camera with id ${firstDeviceId}`)
            // })

            // document.getElementById('resetButton').addEventListener('click', () => {
            //     document.getElementById('result').textContent = '';
            //     codeReader.reset();

            //     console.log('Reset.')
            // })

        })
        .catch((err) => {
            console.error(err)
        })
})

